import MovieBanner from './components/MovieBanner';
import TrendingMovies from './components/TrendingMovies';

async function getMovies() {
  const API_KEY = 'eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIxY2ViYTE1YzhlOTMwNmExNGMxZWQ3ZDUyYTRlNGFhMCIsIm5iZiI6MTczMjYxMjEwNC4xMTAzNDA0LCJzdWIiOiI2NzQ1OGRkMzgwYjQ0YTg5MzdiN2MzNDUiLCJzY29wZXMiOlsiYXBpX3JlYWQiXSwidmVyc2lvbiI6MX0.f5pYhZOw9kt_ZFyPzWay-D1seZ2dOGJ43W7Mb5-a-A0';

  const options = {
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${API_KEY}`,
      'Content-Type': 'application/json'
    }
  };

  try {
    const res = await fetch('https://api.themoviedb.org/3/discover/movie', options);
    const data = await res.json();
    return data.results;
  } catch (error) {
    console.error('Error:', error);
    return [];
  }
}

export default async function Home() {
  const movies = await getMovies();

  return (
    <main className="min-h-screen bg-black">
      <MovieBanner movie={movies[0]} />
      <div className="container mx-auto px-4 py-8">
        <h2 className="text-2xl font-bold text-white mb-6">Upcoming Movies</h2>
        <TrendingMovies movies={movies.slice(1)} /> {/* Pass all movies except the first one */}
      </div>
    </main>
  );
}